import Vue from 'vue'
import App from './App.vue'
import router from './router'
import '@/untils/filter'; //过滤器函数
import 'amfe-flexible'
import './assets/scss/global.scss';
Vue.config.productionTip = false;
router.afterEach(() => {
	window.scrollTo(0,0);
});
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
